.Welcome {
  background: #f0f6ff;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 48px;
}

.wrapper_top {
  display: flex;
  flex-direction: column;
  align-items: center; /* 얘를 안 쓰면 로고의 크기가 div 크기에 맞게 커짐. 왜 그런거지..? */
  gap: 20px;
}

.wrapper_bottom {
  display: flex;
  justify-content: center;
}

.title {
  color: #5b74e6;
  font-family: NanumSquare Neo;
  font-size: 3.6rem;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  margin: 0;
}

.intro {
  color: #707070;
  font-family: Pretendard Variable;
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}

.start_btn {
  width: 180px;
  height: 60px;
  background: #5b74e6;
  border: none;
  border-radius: 100px;

  /* 버튼 내 폰트 */
  color: white;
  font-size: 1.8rem;
  font-family: Pretendard Variable;
  font-weight: 600;
  word-wrap: break-word;
}

.start_btn:hover {
  background: #3353e0;
  cursor: pointer;
}

/* 버튼 클릭 효과 */
.start_btn:active {
  background: #5b74e6;
}
